.App {
  text-align: center;
}



.App-title {
  font-size: 1.5em;
}

body {
  background: brown;
  color: wheat;
}
h3 {
  display: none;
  font-weight: 400;
  font-size: 20px;
}
span {
  font-size: 1.2rem;
  color: brown;
}
.App {
  padding: 20px;
  width: 400px;
  height: 600px;
  margin: 10px auto;
  background: black;
}
input {
  width: 25%;
  height: 24px;
  margin: 10px auto 0;
  opacity: 1;
}
button {
  width: 25%;
  height: 40px;
  background: wheat;
  color: brown;
  font-size: 18px;
  font-weight: 600;
  border-radius: 5px;
  -webkit-transition: .7s ease-in-out;
  -o-transition: .7s ease-in-out;
  transition: .7s ease-in-out;

}
button:hover {
  background: black;
  color: wheat;

}


